.widgetSm {
    /* height: 330px;
    overflow-y: auto; */
    flex: 1;
    border-radius: 10px;
    padding: 20px;
    margin-right: 20px;
    background-color: #fff;
    /* box-shadow: 1px -1px 16px 2px rgba(0, 0, 0, 0.49);
    -webkit-box-shadow: 1px -1px 16px 2px rgba(0, 0, 0, 0.49);
    -moz-box-shadow: 1px -1px 16px 2px rgba(0, 0, 0, 0.49); */
    box-shadow: 0px 0px 7px 0px rgba(0,0,0,1);
    -webkit-box-shadow: 0px 0px 7px 0px rgba(0,0,0,1);
    -moz-box-shadow: 0px 0px 7px 0px rgba(0,0,0,1);
}

.widgetSmTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetSmImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetSmList {
    margin: 0;
    padding: 0;
    list-style: none;
}

.widgetSmListItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}

.widgetSmUser {
    display: flex;
    flex-direction: column;
}

.widgetSmUsername {
    font-weight: 600;
}

.widgetSmUserTitle {
    font-weight: 300;
}

.widgetSmButton {
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    cursor: pointer;
    background-color: #eeeef7;
    color: #555;
}

.widgetSmIcon {
    font-size: 16px;
    margin-right: 5px;
}